.header {
    background-color: whitesmoke;
    min-height: 100px;
    width: inherit;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    margin-bottom: 50px;
}

.header a {
    color: #4a4a4a;
}

.logo {
    flex: 4 1;
    margin-left: 100px;
    margin-right: 10px;
}

.nav-links {
    flex: 2 1;
}

.nav-links a {
    margin-right: 50px;
}
* {
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
    color: #4a4a4a;
    line-height: 1.8;
}

.main {
    margin: 0 15%;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.home-logo {
    height: 400px;
}

.desc {
    margin-top: 50px
}

.header-desc {
    line-height: 1.4
}

.brief-desc {
    font-size: 18px;
}
.work {
    margin: 0 30%;
    align-content: center;
    align-self: center;
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 50px;
}

.work-duration {
    color: rgb(155, 155, 155);

}

.work-header {
    margin:0px;
    line-height: 0.9;
}

.work-content h2 {
    color: #f7c0ab;
    font-weight: 300;
    line-height: 0.6;
}

.work-divider {
    border-color: #f7c0ab;
    width: 40vmax;
}

